/* General styles */
body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f8f9fa; /* Light gray background */
  color: #333; /* Dark text color */
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

/* Header styles */
.App-header {
  background-color: #343a40; /* Dark background for header */
  color: #fff; /* White text color */
  padding: 20px;
  text-align: center;
}

.App-title {
  font-size: 2rem;
  margin-bottom: 10px;
}

/* Navbar styles */
.navbar {
  background-color: #007bff; /* Blue navbar background */
  padding: 10px;
}

.navbar-brand {
  color: #fff; /* White text color for navbar brand */
  font-size: 1.5rem;
  text-decoration: none;
}

.navbar-nav {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.nav-item {
  display: inline-block;
  margin-right: 10px;
}

.nav-link {
  color: #fff; /* White text color for navbar links */
  text-decoration: none;
  padding: 5px 10px;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.nav-link:hover {
  background-color: #0056b3; /* Darker blue on hover */
}

/* Main content styles */
.content {
  padding: 20px;
}

/* Footer styles */
.footer {
  background-color: #343a40; /* Dark background for footer */
  color: #fff; /* White text color for footer */
  text-align: center;
  padding: 20px;
}

/* Media query for responsiveness */
@media (max-width: 768px) {
  .navbar-nav {
    text-align: center;
  }
  .nav-item {
    display: block;
    margin-bottom: 10px;
  }
}
